.about-container {
    background-color: white;
    min-height: 100vh;
    width: 100%;
    box-sizing: border-box;
    color: black;
    display: flex;
    flex-direction: column;
    opacity: 0.95;
    padding-top: 8%;
    padding-left: 4%;

}

.about-me {
    text-align: justify;
}
.body {
    background-color: white;
}
* {
    margin: 0;
    padding: 0;
}
.images {
    position:fixed;
    width: 100%;
    bottom: 0;
}

.images img:nth-child(1) {
    width: 50%;
    height: 50vh;
}
.background {
    display: flex;
    justify-content: flex-start;
    z-index: -10;
}
.images img:nth-child(2) {
    width: 40%;
    height: 50vh;
}
.link {
    text-decoration: none;
    outline: none;
    margin: 0;
    padding: 0;
    border: 0;
}

.contact {
    padding-top: 1%;
}

.contact * {
    padding-top: 1%;
}
.skill-style {
    width: 62px;
    height: 65.38px;
}