.head-contanier {
display: flex;
color: white;
flex-direction: column;
align-items: center;
justify-content: center;
box-sizing: border-box;
max-height: 100vh;
overflow:hidden;
background: linear-gradient( 39.97deg, #458ff6, #458ff6 79.29%);
box-sizing: border-box;
margin: 0;
padding: 0;
border: 0;  
}
div > * {
  font-style: normal;
  font-family:Poppins;
}
button {
    border-radius: 10px;
    color: black;
    background-color: white;

 }
 .header-text {
     display: flex;
     flex-direction: column;
     text-align: center;
     padding-top: 14%;
 }
 .header-text>h1 {
     font-family: Poppins;
     font-style: normal;
     font-weight: 900;
     font-size: 3em;
     line-height: 62px;
     text-align: center;
     letter-spacing: 0.2px;
     margin: 10px;
 }

 .header-text>p {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.2px;
    margin: 10px;
    color: #e5e5e5;
}

.head-btns {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    margin:35px 10px;
    text-decoration: none;
}

.btn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0px 36px;
    height: 52px;
    left:0;
    top:0px;
    text-decoration: none;
    border-radius: 36px;
    background-color: white;
    margin: 4px;
}
.head-anime {
    margin-bottom: -80px;
}

/*
For small screen size
*/

@media only screen  and (max-height:550px){
    .home-anime {
        width: 400px;
    }
}
@media only screen  and (max-width:550px){
    .home-anime {
        width: 80%;
        
    }
    .header-text h1 {
        font-size: 2em;
    }
}