.intro-container {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;

}
.intro-container p {
    font-weight: 1;
}
.intro-container * {
    padding: 1px;
}

@media only screen  and (max-width:550px){
    .intro-container {
        width: 80%;
        padding-top: 14%;
        padding-bottom: 14%;
    }
}
.about-me p {
    font-weight: 1;
}
.about-me {
    width: 60%;
    padding: 1%;
    text-align: left;
    padding-bottom: 3%;
}