body {
    min-height: 100vh;
    width: 100%;
    box-sizing: border-box;
    overflow-x: hidden;
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    border: 0;
    background: linear-gradient( 39.97deg, #458ff6, #458ff6 79.29%);
    font-family: "Sofia", sans-serif;
}
body *{
    margin: 0;
    padding: 0;
  }
input:focus,
textarea:focus,
select:focus {
    outline: hidden;
}