.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    left: 36px;
    top: 36px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #373a47;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background:white;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: white;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100vh;
    overflow-x:  hidden;
    box-shadow: 0px 4px 31px rgba(0,0,0,0.15);
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: linear-gradient( 39.97deg, #03d6f3, #458ff6 79.29%);
      padding: 2.5em 1.5em 0 0;
      height: 100vh;
      overflow:hidden;
  }
  

  
  /* Wrapper for item list */
  .bm-item-list {
    padding: 0.8em;
  }
  
  /* Individual item */
  
  .menu-item {
      position: relative;
      font-size: 2em;
      text-decoration: none;
      color:#fff;
      font-family: 'Poppins', sans-serif;
      font-style: normal;
      font-weight: normal;
      padding: 10px;
  }

  .menu-item:before {
content: '';
position: absolute;
width: 100%;
height: 0;
bottom: 0;
left: 0;
background-color: #fff;
visibility: hidden;
transition: all 0.5s ease-in-out;
  }

  .active-item {
      font-weight: 500;
  }

  .active-item::before,.menu-item:hover:before {
      visibility: visible;
      height: 3px;
  }