.model-container {
    background-color: white;
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 80%;
    top:30%;
    left:10%;
    min-height: 20vh;
    font-family: "Sofia", sans-serif;
    box-sizing: border-box;
    color: black;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  }
  .head-model {
    margin: 2%;
  }
  .head-model * {
    padding-bottom: 1%;
  }
  .middle-model * {
    padding-bottom: 1%;
  }
  .middle-model {
    margin: 2%;
  }
  .mybutton {
    border-radius: 0;
    margin-left: 1%;
    text-decoration: none;
    outline: none;
    padding: 0.5%;
    color: white;
  }
  .end-model {
    display: flex;
    justify-content: flex-end;
    margin-right: 1%;
  }
  .red {
    background-color: rgb(192, 83, 83);
  }
  .green {
    background-color: rgb(93, 209, 93);
  }
